import React, { Component } from "react";
import Place from "../Place/Place";
import axios from "axios";
import { Row } from "antd";

const base_url = "http://localhost:3000/api/place";

class Home extends Component {
  constructor() {
    super();
    this.state = {
      places: []
    };
  }

  componentWillMount() {
    axios
      .get(`${base_url}`)
      .then(res => {
        console.log(res.data.places);
        this.setState({ places: res.data.places });
      })
      .catch(err => {
        console.log(err);
      });
  }

  handleChange = e => {
    let { form, dirty } = this.state;
    let field = e.target.name;
    form[field] = e.target.value;
    dirty = true;
    this.setState({ form, dirty });
  };

  render() {
    const { places } = this.state;
    return (
      <div className="container">
        <h1>Home</h1>
        <Row gutter={16}>
          {places.map((place, i) => (
            <Place key={i} place={place} />
          ))}
        </Row>
      </div>
    );
  }
}

export default Home;
